import React from 'react'
import styles from './Paginator.module.css'
import { useState } from 'react';

function Paginator({ totalCount, pagesSizes, currentPage, onPageChanged }) {
    let pageCounts = Math.ceil(totalCount / pagesSizes)
    let pages = [];
    for (let i = 1; i < pageCounts; i++) {
        pages.push(i)
    }

    let portionCounts = Math.ceil(pageCounts / 6)
    let [portionNumber, setPortionNumber] = useState(1)
    let leftPortionPageNumber = (portionNumber - 1) * 6 + 1
    let rightPortionNumber = portionNumber * 6


    return <div className={styles.paginator}>
        {portionNumber > 1 &&
            <button onClick={() => { setPortionNumber(portionNumber - 1) }}>PREV</button>}
        {pages
            .filter(p => p >= leftPortionPageNumber && p <= rightPortionNumber)
            .map(p => {
                return <span
                    className={currentPage === p && styles.selectedPage}
                    onClick={(e) => { onPageChanged(p) }}>{p}</span>
            })}
        {portionCounts > portionNumber &&
            <button onClick={() => { setPortionNumber(portionNumber + 1) }}>NEXT</button>}
    </div>
}

export default Paginator