import { connect } from 'react-redux'
import { follow, unfollow, setCurrentPage, toggleFollowing, getUsers } from '../../Redux/Users-reducer'
import userPhoto from '../../Assets/Images/user.png'
import Users from './Users'
import React from 'react'
import Preloader from '../Common/Preloader/Preloader'
import { withAuthRedirect } from '../../HOC/WithAuthRedirect'
import { compose } from 'redux'
import { getUser, getPageUser, getUserCount, getCurrentPage, getIsFetching, getFollowingProgress} from '../../Redux/Users-selectors'

class UsersAPIComponent extends React.Component {
    componentDidMount() {
        const {currentPage,pagesSizes} = this.props
        this.props.getUsers(currentPage, pagesSizes)
    }

    onPageChanged(pageNumber) {
        const {pagesSizes} = this.props 
        this.props.getUsers(pageNumber, pagesSizes)
    }

    render() {
        return <>
            {this.props.isFetching ?
                <Preloader />
                : null}
            <Users
                // {...this.props}
                userPhoto={userPhoto}
                onPageChanged={this.onPageChanged.bind(this)}
                currentPage={this.props.currentPage}
                pagesSizes={this.props.pagesSizes}
                totalCount={this.props.totalCount}
                users={this.props.users}
                follow={this.props.follow}
                unfollow={this.props.unfollow}
                followingProgress={this.props.followingProgress}
            />
        </>
    }
}

let mapStateProps = (state) => {
    return {
        users: getUser(state),
        pagesSizes: getPageUser(state),
        totalCount: getUserCount(state),
        // pagesSizes: state.usersPage.pagesSizes,//getPageUser(state),
        // totalCount: state.usersPage.totalCount,//getUserCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingProgress: getFollowingProgress(state)
    }
}

export default compose(
    withAuthRedirect,
    connect(mapStateProps, {
        follow,
        unfollow,
        setCurrentPage,
        toggleFollowing,
        getUsers
    })
)(UsersAPIComponent)

